import {Injectable} from "@angular/core";
import {ApiEditorComponentFeatures} from "../_models/features.model";

/**
 * A service providing document related functionality, including the ability to
 * easily access the current document being edited and document related notifications
 * such as when it is changed.
 */
@Injectable()
export class FeaturesService {

    private features: ApiEditorComponentFeatures;

    constructor() {
        this.features = new ApiEditorComponentFeatures();
    }

    public setFeatures(features: ApiEditorComponentFeatures): void {
        this.features = features;
    }

    public getFeatures(): ApiEditorComponentFeatures {
        return this.features;
    }

}
